@import './App';
.taskForm {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
  margin-bottom: 40px;
  &__input {
    width: 350px;
    height: 40px;
    margin-right: 20px;
    font-size: 22px;
    border-radius: 10px;
    padding: 9px;
  }
  &__btn {
    width: 100px;
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
    text-align: center;
    box-shadow: 0 10px 10px hsl(0, 0%, 3%);
  }
}
